//
document.addEventListener('DOMContentLoaded', function () {
   let lazyImages = [].slice.call(document.querySelectorAll('img[loading=lazy]'));
   let active = false;
   const lazyLoad = function () {
      if (active === false) {
         active = true;
         setTimeout(function () {
            lazyImages.forEach(function (lazyImage) {
               if ((lazyImage.getBoundingClientRect().top <= window.innerHeight && lazyImage.getBoundingClientRect().bottom >= 0) && getComputedStyle(lazyImage).opacity !== '0') {
                  lazyImage.src = lazyImage.dataset.src;
                  lazyImage.srcset = lazyImage.dataset.srcset;
                  lazyImages = lazyImages.filter(function (image) {
                     return image !== lazyImage;
                  });
                  if (lazyImages.length === 0) {
                     document.removeEventListener('scroll', lazyLoad);
                     window.removeEventListener('resize', lazyLoad);
                     window.removeEventListener('orientationchange', lazyLoad);
                  }
               }
            });
            active = false;
         }, 100);
      }
   };
   document.addEventListener('scroll', lazyLoad);
   window.addEventListener('load', lazyLoad);
   window.addEventListener('resize', lazyLoad);
   window.addEventListener('orientationchange', lazyLoad);
});
// Header Burger Menu
if (document.querySelector('.burger')) {
   function burgerMenu() {
      const burger = document.querySelector('.burger')
      const menu = document.querySelector('.nav__menu')
      const body = document.querySelector('body')
      const menuItems = menu.querySelectorAll('.nav__list-item')
      burger.addEventListener('click', () => {
         if (!menu.classList.contains('active')) {
            menu.classList.add('active')
            burger.classList.add('active')
            body.classList.add('locked')
         } else {
            menu.classList.remove('active')
            burger.classList.remove('active')
            body.classList.remove('locked')
         }
      })
      for (const menuItem of menuItems) {
         menuItem.addEventListener('click', () => {
            menu.classList.remove('active')
            burger.classList.remove('active')
            body.classList.remove('locked')
         })
      }
   }
   burgerMenu();
}
// SVGInject
if (document.querySelector('img.svg')) {
   SVGInject(document.querySelectorAll("img.svg"));
}
// Anchor Scroll links
if (document.querySelector('a[href^="#"]')) {
   const anchorlinks = document.querySelectorAll('a[href^="#"]')
   for (const item of anchorlinks) { 
      item.addEventListener('click', (e) => {
         const hashval = item.getAttribute('href')
         const target = document.querySelector(hashval)
         target.scrollIntoView({
            behavior: 'smooth',
            block: 'start'
         })
         history.pushState(null, null, hashval)
         e.preventDefault()
      })
   }
}
// Blog Swiper
if (document.querySelector('.blog__swiper')) {
   new Swiper('.blog__swiper', {
      direction: 'horizontal',
      loop: !0,
      speed: 1000,
      slidesPerView: 4,
      spaceBetween: 24,
      breakpoints: {
         1281: {
            slidesPerView: 4,
         },
         1110: {
            slidesPerView: 3.5,
         },
         992: {
            slidesPerView: 3,
         },
         768: {
            slidesPerView: 2.5,
         },
         600: {
            slidesPerView: 2,
         },
         480: {
            slidesPerView: 1.5,
         },
         0: {
            slidesPerView: 1,
         },
      },
      pagination: {
         el: '.blog-pagination',
         clickable: true,
      },
      keyboard: {
         enabled: !0,
         onlyInViewport: !1
      },
   });
}
// Reviews swiper
if (document.querySelector('.reviews__swiper')) {
   new Swiper('.reviews__swiper', {
      direction: 'horizontal',
      loop: !0,
      speed: 1000,
      slidesPerView: 1,
      spaceBetween: 24,
      pagination: {
         el: '.reviews-pagination',
         clickable: true,
      },
      keyboard: {
         enabled: !0,
         onlyInViewport: !1
      },
   });
}
// Services Accordion
if (document.querySelector('.accordion__header')) {
   function accordion() {
      const items = document.querySelectorAll('.accordion__header')
      items.forEach(item => {
         item.addEventListener('click', () => {
            const parent = item.parentNode
            if (parent.classList.contains('show')) {
               parent.classList.remove('show')
            } else {
               document
                  .querySelectorAll('.accordion__item')
                  .forEach(child => child.classList.remove('show'))
               parent.classList.add('show')
            }
         })
      })
   }
   accordion()
}
